.alertBackground {
    position: fixed;
    top: 0%;
    right: 0%;
    bottom: 0%;
    left: 0%;
    background-color: rgba(0, 0, 0, 0.5);
}

.alert {
    position: fixed;
    top: 40%;
    right: 30%;
    bottom: 40%;
    left: 30%;
    z-index: 2000;
    background-color: rgb(110, 144, 255);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding: 1%;
}


